/* Contact Section */
#contact {
    background-color: #ffffff;
    padding: 40px 20px;
    text-align: center;
    margin-bottom: 100px; /* Prevent overlap with fixed footer */
}

#contact h2 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 30px;
}

/* Form Styling */
form {
    max-width: 600px;
    margin: 0 auto;
    text-align: left;
}

form input,
form textarea {
    width: 100%;
    padding: 15px;
    margin: 10px 0 20px 0;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

form input:focus,
form textarea:focus {
    border-color: #007BFF;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.3);
}

form textarea {
    resize: vertical;
    min-height: 120px;
}

form button {
    padding: 12px 24px;
    font-size: 1rem;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

form button:hover {
    background-color: #0056b3;
}

/* Contact Info Below Form */
#contact p {
    margin-top: 20px;
    font-size: 1rem;
}

#contact a {
    color: #007BFF;
    text-decoration: none;
}

#contact a:hover {
    text-decoration: underline;
}

